import React from "react";
import {
  Container,
  Row,
  Col,
  ListGroup,
  ListGroupItem,
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  Alert,
  InputGroup,
  InputGroupAddon,
  Badge,
} from "reactstrap";

import isEqual from "lodash.isequal";
import * as Extensions from "trimble-connect-project-workspace-api";

function inIframe() {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mainMenu: {
        title: "Test React app",
        icon: `https://components.stage.connect.trimble.com/trimble-connect-project-workspace-api/logo192.png`,
        command: "main_nav_menu_cliked",
      },
      subMenuItems: [
        {
          title: "Sub menu 1",
          icon: `https://components.stage.connect.trimble.com/trimble-connect-project-workspace-api/logo192.png`,
          command: "submenu_1_clicked",
        },
        {
          title: "Sub menu 2",
          icon: `https://components.stage.connect.trimble.com/trimble-connect-project-workspace-api/logo192.png`,
          command: "submenu_2_clicked",
        },
      ],
      queryParams: "?taskId=16&navigate=true",
      editParams: false,
    };
  }

  async componentDidMount() {
    const { mainMenu, subMenuItems = [] } = this.state;
    if (inIframe()) {
      this.API = await Extensions.connect(
        window.parent,
        (event, args) => {
          switch (event) {
            case "extension.command":
              this.setState({
                alertMessage: `Command executed by the user {command:${args.data}}`,
              });
              break;
            case "extension.accessToken":
              this.setState({ accessToken: args.data });
              break;
            case "extension.userSettingsChanged":
              this.setState({ alertMessage: `User settings changed!` });
              this.getUserSettings();
              break;
            default:
          }
        },
        30000
      );

      this.API.ui.setMenu({ ...mainMenu, subMenus: [...subMenuItems] }).then();

      //requesting accessToken after 5 secs
      // setTimeout(() => {
      //   this.API.extension.getPermission("accesstoken").then(permission => console.log("extension:react-client:getPermission", permission));
      // }, 5000);
    }
  }
}
export default App;